.Header{
  background-color: #c26bff;
  width: 100%;
  z-index: 10;
}
.Header h1{
  margin: 0;
  border-bottom: 4px solid #9d5bc3;
  text-align: center;
  color: #6e2887;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
}
.Header a{
  text-decoration: none;
}
.Header h1:hover{
  background: -webkit-gradient(linear, left top, right top, color-stop(10%, #d667ff), color-stop(50%, #e095ff), color-stop(90%, #d667ff));
  background: -webkit-linear-gradient(left, #d667ff 10%, #e095ff 50%, #d667ff 90%);
  background: -o-linear-gradient(left, #d667ff 10%, #e095ff 50%, #d667ff 90%);
  background: linear-gradient(90deg, #d667ff 10%, #e095ff 50%, #d667ff 90%);
  color: #331941;
}
.Header h1:active{
  color: #f3edff;
}
.Header__additional{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.Header__options, .Header__favorites, .Header__back{
  position: relative;
  padding: 5px;
  margin: 5px 0;
  background-color: #d981ff;
  border-right: 5px solid #9d5bc3;
  border-bottom: 5px solid #6d4991;
  border-top: 1px solid #bd62e6;
  border-left: 1px solid #bd62e6;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  text-align: center;
}
.Header__options:hover, .Header__favorites:hover, .Header__back:hover{
  background-color: #e1a4ff;
}
.Header__btn-active{
  background-color: #d981ff;
  border: 1px solid #6d4991;
}
.Header__options{
  -ms-flex-order: 2;
      order: 2;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.Header__favorites:active, .Header__back:active{
  border: 1px solid #6d4991;
}
.Header__sidebar{
  -ms-flex-order: 3;
      order: 3
}
.Header__favorites{
  -ms-flex-order: 4;
      order: 4;
  padding: 0;
}
.Header__favorites a{
  display: block;
  padding: 5px;
}
.Header__favorites-nonbtn{
  margin-top: 10px;
}
.Header__favorites-nonbtn:hover{
  background-color: #d981ff;
}
.Header__back a{
  display: block;
}
.Header__search{
  -ms-flex-order: 1;
      order: 1;
  -ms-flex-positive: 2;
      flex-grow: 2;
  margin: 10px;
}
.Header__search input{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #f0efff;
  padding: 2px 10px;
  width: 100%;
  outline: none;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.Header__search input:focus{
  border-bottom: 2px solid black;
}

@media (min-width: 800px) {
  .Header__additional{
    -ms-flex-direction: row;
        flex-direction: row;
    position: relative;
  }
  .Header__options, .Header__favorites, .Header__back{
    position: relative;
    padding: 5px;
    margin: 5px 0;
    background-color: #d981ff;
    border-right: 5px solid #9d5bc3;
    border-bottom: 5px solid #6d4991;
    border-top: 1px solid #bd62e6;
    border-left: 1px solid #bd62e6;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
  }
  .Header__options:hover, .Header__favorites:hover, .Header__back:hover{
    background-color: #e1a4ff;
  }
  .Header__options{
    -ms-flex-order: 1;
        order: 1;
    width: 190px;
    height: 18px;
    margin-right: 10px;
  }
  .Header__sidebar{
    position: absolute;
    left: 0;
    top: 45px;
    -ms-flex-order: 2;
        order: 2
  }
  .Header__favorites{
    -ms-flex-order: 3;
        order: 3;
    padding: 0 5px;
    width: 190px;
    height: 28px;
  }
  .Header__search{
    -ms-flex-order: 4;
        order: 4;
    -ms-flex-positive: 2;
        flex-grow: 2;
    margin: 10px 0 0 10px;
    height: 28px;
  }
  .Header__back{
    width: 190px;
    height: 18px;
    padding: 5px;
    margin-right: 20px;
  }
  .Header__back a{
    padding: 0;
    margin: 0;
  }
  .Header__favorites:active, .Header__back:active{
    border: 1px solid #6d4991;
  }
  .Header__btn-active{
    background-color: #d981ff;
    border: 1px solid #6d4991;
    margin-bottom: 225px;
  }
  .Header__favorites-nonbtn{
    margin-bottom: 5px;
  }
  .Header__additional--movie{
    position: relative;
  }
}

@media (min-width: 1300px) {
  .Header h1{
    font-size: 48px;
  }
  .Header__additional{
    font-size: 24px;
  }
  .Header__sidebar{
    top: 51px;
  }
  .Header__options, .Header__favorites, .Header__back, .Header__search{
    height: 27px;
    padding: 5px;
  }
  .Header__options, .Header__favorites, .Header__back{
    width: 220px;
  }
  .Header__options:active{
    border: 1px solid #6d4991;
  }
  .Header__search{
    margin-top: 5px;
  }
  .Header__search input{
    padding: 5px 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    font-size: 16px;
  }
  .Header__options a, .Header__favorites a, .Header__back a {
    padding: 0;
  }
  .Header__btn-active{
    margin-bottom: 280px;
  }
  .Header__favorites-nonbtn{
    margin-bottom: 10px;
    padding: 0 0 10px 0;
  }
}


