.container {
  min-width: 320px;
  width: 320px;
  padding: 10px;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}
.container--movielist, .container--movieinfo {
  -webkit-box-shadow: 20px 10px 28px -4px rgba(120, 68, 255, 0.2), -20px 10px 28px -4px rgba(120, 68, 255, 0.2);
          box-shadow: 20px 10px 28px -4px rgba(120, 68, 255, 0.2), -20px 10px 28px -4px rgba(120, 68, 255, 0.2);
  min-height: calc(95vh - 123px);
}
.container--header{
  background-color: #d667ff;
}
.container--imageshow{
  background: -webkit-gradient(linear, left top, left bottom, from(#bb5dff), to(#dc9dff));
  background: -webkit-linear-gradient(top, #bb5dff, #dc9dff);
  background: -o-linear-gradient(top, #bb5dff, #dc9dff);
  background: linear-gradient(to bottom, #bb5dff, #dc9dff);
  position: relative;
  padding: 20px 5px 0 5px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  min-width: 300px;
  width: 300px;
}

@media (min-width: 800px) {
  .container {
    width: 800px;
  }
  .container--header{
    padding-bottom: 0;
    z-index: 5;
  }
  .container--imageshow{
    width: 560px;
  }
}

@media (min-width: 1300px) {
  .container {
    width: 1300px;
  }
  .container--imageshow{
    width: 900px;
  }
}


