.FaveBtn{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #e6ecff;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  width: 100%;
  height: 30px;
  z-index: 5;
}
.FaveBtn--active{
  background: -webkit-gradient(linear, left top, right top, from(#6CFFE4), to(#54FFAD));
  background: -webkit-linear-gradient(left, #6CFFE4, #54FFAD);
  background: -o-linear-gradient(left, #6CFFE4, #54FFAD);
  background: linear-gradient(to right, #6CFFE4, #54FFAD);
  border-bottom: 2px solid #4ED78B;
}
.FaveBtn--active:hover{
  background: -webkit-gradient(linear, left top, right top, from(#6af5db), to(#51f5a6));
  background: -webkit-linear-gradient(left, #6af5db, #51f5a6);
  background: -o-linear-gradient(left, #6af5db, #51f5a6);
  background: linear-gradient(to right, #6af5db, #51f5a6);
  border-bottom: 2px solid #4cc379;
}
.FaveBtn__button{
  padding-top: 5px;
  background-color: transparent;
  width: 100%;
  border: none;
  cursor: pointer;
  outline: none;
}
.FaveBtn:hover{
  background-color: #dde2f5;
}
.FaveBtn__button img{
  width: 20px;
  height: 20px;
}
.FaveBtn__moviePage{
  position: absolute;
  top: 100%;
  left: calc(50% - 15px);
  width: 30px;
  background-color: #e6ecff;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
.FaveBtn__moviePage::before{
  position: absolute;
  left: -135px;
  display: block;
  content: "";
  width: 300px;
  height: 5px;
  background-color: #e6ecff;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
.FaveBtn__moviePage:hover{
  background-color: #dde2f5;
}
.FaveBtn__moviePage:hover::before{
  background-color: #dde2f5;
}
.FaveBtn__moviePage .FaveBtn__button{
  padding: 0;
  margin-top: 5px;
  padding-bottom: 5px;
}
.FaveBtn__moviePage--active{
  background-color: #6CFFE4;
}
.FaveBtn__moviePage--active::before{
  background-color: #6CFFE4;
}
.FaveBtn__moviePage--active:hover{
  background-color: #6af5db;
}
.FaveBtn__moviePage--active:hover::before{
  background-color: #6af5db;
}
.FaveBtn__header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #6af5db;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  z-index: 0;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

@media (min-width: 800px) {
  .FaveBtn__moviePage .FaveBtn__button{
    margin-top: 8px;
  }
  .FaveBtn__moviePage{
    width: 50px;
    height: 35px;
    left: calc(50% - 25px);
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
  }
  .FaveBtn__moviePage::before{
    left: -365px;
    display: block;
    content: "";
    width: 780px;
    height: 8px;
  }
}
@media (min-width: 1300px) {
  .FaveBtn{
    bottom: 0;
    left: 0;
    height: 50px;
  }
  .FaveBtn__button img{
    width: 35px;
    height: 35px;
  }
  .FaveBtn__moviePage{
    width: 70px;
    height: 50px;
    left: calc(50% - 35px);
  }
  .FaveBtn__moviePage::before{
    left: -605px;
    width: 1280px;
    height: 5px;
  }
}


